'use strict';

// MAIN //

/**
* Matches a character sequence which begins with 'end'.
*
* Regular expression: `/^end/`
*
* -   `^`
*
*     -   start of input
*
* -   `end`
*
*     -   match the character sequence 'end'
*
* @private
* @name RE_END
* @type {RegExp}
*
* @example
* var bool = RE_END.test( 'end' );
* // returns true
*
* bool = RE_END.test( 'foo end' );
* // returns false
*/
var RE_END = /^end/;


// EXPORTS //

export default RE_END;
